<template>
    <MainLayout>
        <div class="settings-page">
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <div class="head-title">Settings</div>
                        <button type="button" class="sign-out btn btn-light" name="button" @click="logoutUser"><i class="material-icons">power_settings_new</i> সাইন আউট</button>
                        <div class="settings-menu">
                            <a href="#" v-on:click="tabchange" class="active" data-tab="profile-settings">Profile</a>
                            <a href="#" v-on:click="tabchange" data-tab="notification-settings">প্রজ্ঞাপন</a>
                            <a href="#" v-on:click="tabchange" data-tab="password-settings">পাসওয়ার্ড আপডেট</a>
                        </div>
                        <div class="menu-contents">
                            <div class="tabs" id="profile-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name">প্রথম নাম পরিবর্তন *</label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-first-name" :value="firstName" @input="updateFirstName" placeholder="প্রথম নাম পরিবর্তন"> -->
                                            <TranslatingInput :value="firstName" :oninput="updateFirstName"></TranslatingInput>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name">অন্তিম নাম পরিবর্তন</label>
                                            <!-- <input type="text" class="form-control" id="pratilipi-settings-last-name" :value="lastName" @input="updateLastName" placeholder="অন্তিম নাম পরিবর্তন"> -->
                                            <TranslatingInput :value="lastName" :oninput="updateLastName"></TranslatingInput>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-first-name-en">প্রথম নাম (ইংরাজীতে) *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-first-name-en" :value="firstNameEn" @input="updateFirstNameEn" placeholder="প্রথম নাম (ইংরাজীতে)">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-last-name-en">অন্তিম নাম</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-last-name-en" :value="lastNameEn" @input="updateLastNameEn" placeholder="অন্তিম নাম">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-pen-name">পদবী *</label>
                                            <input type="text" class="form-control" id="pratilipi-settings-pen-name" :value="penName" @input="updatePenName" placeholder="পদবী">
                                        </div>
                                        <div class="form-group">
                                            <label for="pratilipi-settings-language">ভাষা পছন্দ করুন *</label>
                                            <select class="form-control" id="pratilipi-settings-language" @input="updateLanguage">
                                                <option disabled selected>ভাষা পছন্দ করুন</option>
                                                <option :selected="eachLanguage.fullName.toUpperCase() === language" :value="eachLanguage.fullName.toUpperCase()" v-for="eachLanguage in constants.LANGUAGES" :key="eachLanguage.shortName">{{ eachLanguage.fullName.toUpperCase() }}</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-summary">সংক্ষিপ্ত পরিচয়</label>
                                            <textarea class="form-control" id="pratilipi-settings-summary" :value="summary" @input="updateSummary" rows="3"></textarea>
                                        </div>
                                    </div>
                                    <div class="section-title">Private Information</div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email">ইমেল প্রদান করুন</label>
                                            <input type="email" class="form-control" id="pratilipi-settings-email" :value="email" @input="updateEmail" placeholder="ইমেল প্রদান করুন">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-phone">ফোন</label>
                                            <input type="tel" class="form-control" id="pratilipi-settings-phone" :value="phone" @input="updatePhone" placeholder="ফোন">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-gender">লিঙ্গ</label>
                                            <select class="form-control" id="pratilipi-settings-gender" @input="updateGender">
                                                <option disabled selected>লিঙ্গ</option>
                                                <option :selected="'MALE' === gender" value="MALE">পুরুষ</option>
                                                <option :selected="'FEMALE' === gender" value="FEMALE">নারী</option>
                                                <option :selected="'OTHER' === gender" value="OTHER">অন্যান্য</option>
                                            </select>
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-dateOfBirth">জন্মতারিখ</label>
                                            <input type="date" class="form-control" id="pratilipi-settings-dateOfBirth" :value="dateOfBirth" @input="updateDateOfBirth" placeholder="জন্মতারিখ">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateProfileSettings">পরিবর্তন সুরক্ষিত করুন</button>
                                </form>
                            </div>
                            <div class="tabs" id="notification-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-email-frequency">ই-মেল করুন</label>
                                            <select class="form-control" id="pratilipi-settings-email-frequency">
                                                <option disabled selected>ই-মেল করুন</option>
                                                <option>অবিলম্বে</option>
                                                <option>রোজ</option>
                                                <option>সাপ্তাহিক</option>
                                                <option>প্রতি মাস</option>
                                                <option>কখনো না</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div class="section-title">লেখা সম্বন্ধে</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_review">
                                            <label class="form-check-label" for="notif_option_new_review">নতুন মন্তব্য</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_comment">
                                            <label class="form-check-label" for="notif_option_new_comment">নতুন জবাব</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_review">
                                            <label class="form-check-label" for="notif_option_like_review">মন্তব্য পছন্দ হওয়ায়</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_like_comment">
                                            <label class="form-check-label" for="notif_option_like_comment">জবাব পছন্দ হওয়ায়</label>
                                        </div>
                                    </div>
                                    
                                    <div class="section-title">মিত্র মণ্ডল সম্বন্ধিত</div>
                                    <div class="form-row">
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_new_follower">
                                            <label class="form-check-label" for="notif_option_new_follower">নতুন অনুসরণকারী যুক্ত হওয়ায়</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_published_follower">
                                            <label class="form-check-label" for="notif_option_pratilipi_published_follower">অনুসরণকারীর দ্বারা নতুন লেখা প্রকাশিত হওয়ায়</label>
                                        </div>
                                        <div class="form-check">
                                            <input type="checkbox" class="form-check-input" id="notif_option_pratilipi_updates">
                                            <label class="form-check-label" for="notif_option_pratilipi_updates">প্রতিলিপির পক্ষ থেকে তথ্য এবং প্রস্তাবনা</label>
                                        </div>
                                    </div>
                                    <button type="submit" class="btn btn-primary update-btn" disabled>পরিবর্তন সুরক্ষিত করুন</button>
                                </form>
                            </div>
                            <div class="tabs" id="password-settings">
                                <form>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-current-password">বর্তমান পাসওয়ার্ড *</label>
                                            <input type="password" class="form-control" v-model="oldPassword" id="pratilipi-settings-current-password" placeholder="বর্তমান পাসওয়ার্ড">
                                        </div>
                                    </div>
                                    <div class="form-row">
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-new-password">নতুন পাসওয়ার্ড *</label>
                                            <input type="password" class="form-control" id="pratilipi-settings-new-password" placeholder="নতুন পাসওয়ার্ড">
                                        </div>
                                        <div class="form-group col-md-6 col-sm-12">
                                            <label for="pratilipi-settings-confirm-password">পাসওয়ার্ড অনুমোদন করুন *</label>
                                            <input type="password" class="form-control" v-model="newPassword" id="pratilipi-settings-confirm-password" placeholder="পাসওয়ার্ড অনুমোদন করুন">
                                        </div>
                                    </div>
                                    <button type="button" class="btn btn-primary update-btn" @click="updateUserPassword({ oldPassword, newPassword })">পরিবর্তন সুরক্ষিত করুন</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </MainLayout>
</template>

<script>
import MainLayout from '@/layout/main-layout.vue';
import TranslatingInput from '@/components/TranslatingInput.vue';
import constants from '@/constants';
import { mapGetters, mapActions, mapState } from 'vuex'

export default {
    components: {
        MainLayout,
        TranslatingInput
    },
    data() {
        return {
            constants,
            oldPassword: '',
            newPassword: ''
        }
    },
    computed: {
        ...mapGetters('settingspage', [
            'getAuthorData'
        ]),
        ...mapState({
            email: state => state.user.data.email,
            phone: state => state.user.data.phone,
            firstName: state => state.settingspage.author.data.firstName,
            language: state => state.settingspage.author.data.language,
            lastName: state => state.settingspage.author.data.lastName,
            firstNameEn: state => state.settingspage.author.data.firstNameEn,
            lastNameEn: state => state.settingspage.author.data.lastNameEn,
            penName: state => state.settingspage.author.data.penName,
            summary: state => state.settingspage.author.data.summary,
            gender: state => state.settingspage.author.data.gender,
            dateOfBirth: state => state.settingspage.author.data.dateOfBirth
        }),
        ...mapGetters([
            'getUserDetails',
            'getLogoutStatus'
        ]),
    },
    methods: {
        ...mapActions('settingspage', [
            'fetchAuthorDetails',
            'updateUserDetails',
            'updateAuthorDetails',
            'updateUserPassword'
        ]),
        ...mapActions([
            'logoutUser'
        ]),
        tabchange(event) {
            event.preventDefault();        
            var tab_id = $(event.currentTarget).attr('data-tab');
            $(".settings-menu a").removeClass("active");
            $(event.currentTarget).addClass("active");
            $(".tabs").hide();
            $("#" + tab_id).show();
        },
        updateEmail(e) { this.$store.commit('settingspage/updateEmail', e.target.value) },
        updatePhone(e) { this.$store.commit('settingspage/updatePhone', e.target.value) },
        updateFirstName(value) { this.$store.commit('settingspage/updateFirstName', value) },
        updateLanguage(e) { this.$store.commit('settingspage/updateLanguage', e.target.selectedOptions[0].value) },
        updateLastName(value) { this.$store.commit('settingspage/updateLastName', value) },
        updateFirstNameEn(e) { this.$store.commit('settingspage/updateFirstNameEn', e.target.value) },
        updateLastNameEn(e) { this.$store.commit('settingspage/updateLastNameEn', e.target.value) },
        updatePenName(e) { this.$store.commit('settingspage/updatePenName', e.target.value) },
        updateSummary(e) { this.$store.commit('settingspage/updateSummary', e.target.value) },
        updateGender(e) { this.$store.commit('settingspage/updateGender', e.target.selectedOptions[0].value) },
        updateDateOfBirth(e) { this.$store.commit('settingspage/updateDateOfBirth', e.target.value) },
        updateProfileSettings() {
            this.updateUserDetails();
            this.updateAuthorDetails();
        }
    },
    watch: {
        'getUserDetails.authorId'(newValue) {
            if (newValue) {
                this.fetchAuthorDetails(newValue);
            }
        },
        'getUserDetails.isGuest'(isGuest) {
            if (isGuest) {
                this.$router.push('login');
            }
        },
        'getLogoutStatus'(loggedOut) {
            if (loggedOut) {
                location.reload()    
            }
        }
    },
    created() {
        if (this.getUserDetails.isGuest) {
            this.$router.push('login');
        }

        if (this.getUserDetails.authorId) {
            this.fetchAuthorDetails(this.getUserDetails.authorId);    
        }
    }
}
</script>

<style lang="scss" scoped>
.settings-page {
    margin-top: 85px;
    text-align: left;
    @media screen and (max-width: 992px ) {
        margin-top: 65px;
    }
    .head-title {
        font-size: 18px;
        font-weight: bold;
        text-align: left;
        border-left: 3px solid #d0021b;
        padding-left: 10px;
        margin: 10px 0 0;
        float: left;
    }
    .sign-out {
        display: inline-block;
        color: #d0021b;
        float: right;
        margin-top: 10px;
        font-size: 14px;
        i {
            vertical-align: middle;
            font-size: 18px;
        }
    }
    .settings-menu {
        border-bottom: 1px solid #e9e9e9;
        padding: 8px 0 10px;
        text-align: left;
        overflow: hidden;
        width: 100%;
        overflow-x: auto;
        white-space: nowrap;
        clear: both;
        a {
            color: #555;
            font-size: 13px;
            border-bottom: 2px solid #fff;
            padding: 5px 5px 11px;
            span {
                font-size: 12px;
                font-weight: bold;
                padding: 0 2px;
                margin-right: 5px;
                display: inline-block;
                text-align: center;
            }
            &.active {
                color: #d0021b;
                border-color: #d0021b;
                span {
                    color: #d0021b;
                }
            }
            &:hover {
                text-decoration: none;
                color: #d0021b;
            }
        }
    }
    .tabs {
        margin: 20px 0;
        display: none;
        &#profile-settings {
            display: block;
        }
        label {
            font-size: 12px;
        }
        .section-title {
            font-size: 16px;
            margin: 10px 0;
            font-weight: bold;
        }
        .form-check {
            width: 100%;
            margin: 0 10px 5px 15px;
            label {
                font-size: 14px;
                vertical-align: text-bottom;
            }
        }
        .update-btn {
            background: #d0021b;
            border: 0;
            outline: none;
            font-size: 14px;
            float: right;
            margin: 10px 0;
        }
    }
}
</style>



