<template>
    <!-- Writer Modal -->
    <div class="modal fade writermodal" id="writermodal" tabindex="-1" role="dialog" aria-labelledby="writermodalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="writermodalLabel">শিরোনাম যুক্ত করুন</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <i class="material-icons">close</i>
                    </button>
                </div>
                <div class="modal-body">
                    <form>
                        <div class="form-group">
                            <label for="pratilipi_write_language">যেই ভাষাতে আপনি লিখছেন</label>
                            <select class="form-control" id="pratilipi_write_language" v-model="language">
                                <option :value="eachLanguage.fullName.toUpperCase()"  v-for="eachLanguage in constants.LANGUAGES" :key="eachLanguage.shortName">{{ eachLanguage.languageNative }}</option>
                            </select>
                        </div>
                        <div class="form-group">
                            <label for="pratilipi_write_title_input">এখানে শিরোনাম লিখুন *</label>
                            <TranslatingInput :value="title" :oninput="updatePrefilledValue"></TranslatingInput>
                        </div>
                        <div class="form-group">
                            <label for="pratilipi_write_title_en_input">পুনরায় ইংরাজিতে শিরোনাম লিখুন</label>
                            <input type="text" :value="titleEn" @input="($event) => { titleEn = $event.target.value}" class="form-control" id="pratilipi_write_title_en_input" :placeholder="'পুনরায় ইংরাজিতে শিরোনাম লিখুন'">
                        </div>
                        <div class="form-group">
                            <label for="pratilipi_write_type">প্রকার</label>
                            <select class="form-control" id="pratilipi_write_type" v-model="type">
                                <option value="POEM">কবিতা</option>
                                <option value="STORY">গল্প</option>
                                <option value="ARTICLE">প্রবন্ধ</option>
                            </select>
                        </div>
                        <div class="form-check">
                            <input type="checkbox" class="form-check-input" id="agree-terms-conditions">
                            <label class="form-check-label" for="agree-terms-conditions">আমি কপিরাইট নীতি ও পরিষেবার শর্তাদি স্বীকার করলাম</label>
                        </div>
                        <a href="/terms-of-service" class="terms-link" target="_blank">এখানে কপিরাইট নীতি পড়ুন</a>
                        <button type="button" @click="createPratilipiAndGoToWriter({ title, titleEn, type, language })" class="btn btn-submit">পরবর্তী</button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import constants from '@/constants';
import { mapGetters, mapActions } from 'vuex'
import TranslatingInput from '@/components/TranslatingInput.vue';

export default {
    data() {
        return {
            constants,
            title: '',
            titleEn: '',
            language: null,
            type: null
        }
    },
    methods: {
        updatePrefilledValue(value) {
            this.title = value;
        },
        ...mapActions('writepage', [
            'createPratilipiAndGoToWriter'
        ])
    },
    components: {
        TranslatingInput
    }
}
</script>

<style lang="scss" scoped>
.writermodal {
    text-align: left;
    max-width: 800px;
    margin: 50px auto;
    .modal-header {
        padding: 15px 15px 10px;
    }
    .form-group {
        font-size: 14px;
    }
    .form-check label {
        font-size: 12px;
        padding-left: 5px;
    }
    .terms-link {
        font-size: 12px;
        color: #212121;
        border-bottom: 1px solid;
        &:hover {
            text-decoration: none;
        }
    }
    .btn-submit {
        background: #d0021b;
        color: #fff;
        border: 0;
        font-size: 14px;
        float: right;
    }
    .cancel {
        background: none;
        border: 0;
        float: right;
        font-size: 12px;
        line-height: 33px;
    }
}
</style>



