export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'রিপোর্টের বিষয়';
            case 'edit_pratilipi_summary':
            	return 'রচনার সারাংশ';
            case 'edit_pratilipi_title':
            	return 'শীর্ষক';
            case 'pratilipi_delete_content':
                return 'ডিলিট';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['রিপোর্টের বিষয়'];
            case 'edit_pratilipi_summary':
            	return ['সংক্ষিপ্ত সারাংশ লিখুন'];
            case 'edit_pratilipi_title':
            	return ['এখানে শিরোনাম লিখুন', 'পুনরায় ইংরাজিতে শিরোনাম লিখুন'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'আপনি কি রচনাটি  ডিলিট করতে চান?';
        }
    }
}



