<template>
    <div class="modal fade" id="forgotPassModal" tabindex="-1" role="dialog" aria-labelledby="forgotPassModalLabel" aria-hidden="true">
        <div class="modal-dialog" role="document">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title" id="forgotPassModalLabel">যদি আপনি পাসওয়ার্ড ভুলে গিয়ে থাকেন?</h5>
                    <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                        <span aria-hidden="true">&times;</span>
                    </button>
                </div>
                <div class="modal-body">
                    <div id="password_reset">
                        <form>
                            <div class="form-group">
                                <input type="email" @input="forgotten_password_email = $event.target.value" class="form-control" id="forgotEmail" :placeholder="'ইমেল প্রদান করুন'">
                            </div>
                            <button type="button" @click="sendPasswordResetEmail(forgotten_password_email)" class="btn sign-in">নতুন পাসওয়ার্ড বানান</button>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import mixins from '@/mixins';

import { mapGetters, mapActions } from 'vuex'

export default {
    mixins: [
        mixins
    ],
    data() {
        return {
            forgotten_password_email: ''
        }
    },
    methods: {
        ...mapActions([
            'sendPasswordResetEmail'
        ])
    },
    created() {
        
    },
    mounted() {
        
    }
}
</script>

<style lang="scss" scoped>
#forgotPassModal {
    .forms {
        margin: 15px 10px;
        text-align: left;
        display: none;
        .forgot-pass {
            font-size: 12px;
            margin-right: 10px;
            color: #212529;
        }
    }
    .sign-in {
        background: #d00b12;
        color: #fff;
        margin-right: 10px;
        &:hover {
            opacity: 0.9;
        }
    }
}
</style>



